export interface Document {
    uuid: string;
    title: string;
    root: string;
}

export enum GenerationType {
    CONTENT = 'content',
    STUBS = 'stubs',
}

export interface GeneratorDefinition {
    uuid: string;
    name: string;
    mode: GenerationType;
    prompt: string;
}

export interface DocumentNode {
    uuid: string;
    path: string[]; // Always includes this node's UUID
    name: string
    stub: string;
    content: string;
    children: string[];
    metadata: {
        generate_node_prompt: string;
        generate_child_stubs_prompt: string;
    };
}

export interface WritingContextType {
    // Documents
    documents: { [key: string]: Document };
    currentDocument: Document | null;
    createDocument: (title: string) => void;
    setCurrentDocument: (document: Document) => void;
    editDocument: (document: Document, edit_fn: (document: Document) => void) => void;
    saveDocument: () => void;
    loadDocument: (file: File) => void;

    // Nodes
    nodes: { [key: string]: DocumentNode };
    currentNode: DocumentNode | null;
    createNode: (parentUUID: string, properties: Partial<DocumentNode>) => void;
    editNode: (edit_fn: (node: DocumentNode) => void, node?: DocumentNode) => void;
    deleteNode: (node: DocumentNode) => void;
    setCurrentNode: (node: DocumentNode) => void;

    // Helpers
    tools: {
        getParent: (node: DocumentNode) => DocumentNode | null;
        getSiblings: (node: DocumentNode) => DocumentNode[];
        getChildren: (node: DocumentNode) => DocumentNode[];
        getAncestors: (node: DocumentNode) => DocumentNode[];
        getFullText: (node: DocumentNode | null) => string | null;
        // getDescendants: (node: DocumentNode) => DocumentNode[];
    }

    // Generation
    generate: (cur: DocumentNode, generator: GeneratorDefinition) => Promise<void>;
    // generate: (cur: DocumentNode, generationMode: GenerationType, contentPrompt: string, stubPrompt: string) => Promise<void>;
    generators: { [key: string]: GeneratorDefinition };
    createGenerator: () => void;
    editGenerator: (generator: GeneratorDefinition, edit_fn: (generator: GeneratorDefinition) => void) => void;
    deleteGenerator: (generator: GeneratorDefinition) => void;
    // generateNode: (node: DocumentNode, generator: GeneratorDefinition) => Promise<void>;
}