import React, { useState, useEffect } from 'react';
import { useSettings } from '../Settings';

export const AutoScrollChatWindow = ({ height, width, children }: { children: React.ReactNode; height: string; width: string }) => {
    const settings = useSettings();

    const tolerance = 40;
    const [scroll, setScroll] = useState(false);
    const [curTimeout, setCurTimeout] = useState<NodeJS.Timeout | null>(null);

    useEffect(() => {
        // Disable effect if user is actively scrolling right now
        if (scroll) {
            return;
        }

        const chatWindow = document.getElementById('chat-window');
        if (chatWindow &&
            chatWindow.scrollHeight - chatWindow.scrollTop - chatWindow.clientHeight < tolerance) {
            chatWindow.scrollTop = chatWindow.scrollHeight;
        }
    }, [scroll, children]);

    // // When the current node changes, scroll to the bottom
    // useEffect(() => {
    //     const chatWindow = document.getElementById('chat-window');
    //     if (chatWindow) {
    //         // chatWindow.scrollTop = chatWindow.scrollHeight;
    //         // Delay slightly (by 10ms)
    //         setTimeout(() => {
    //             chatWindow.scrollTop = chatWindow.scrollHeight;
    //         }, 10);
    //     }
    // }, [children]);

    return (
        <div
            id="chat-window"
            style={{
                height, width,
                overflowY: "auto",
            }}
            onScroll={(e) => {
                // Set as scroll = true
                console.log("Scrolling");
                setScroll(true);
                // Set as scroll = false after 1 second (unless we're still scrolling)
                if (curTimeout) {
                    clearTimeout(curTimeout);
                }
                setCurTimeout(setTimeout(() => setScroll(false), 200));
            }}
        >
            {children}
        </div>
    );
};
