import React, { useState } from 'react';
import { Box, Button } from '@mui/material';
import { Dropzone } from '../teacher/Dropzone';
import { GPTMenuBar } from '../GPTMenuBar';
import { GenerationModal } from './GenerationModal';
import { useWriting } from '../writing';

export function MenuBar(props: { metaOpen: boolean; handleMetaOpen: () => void; handleMetaClose: () => void; mode: "edit" | "preview"; setMode: (mode: "edit" | "preview") => void; }) {
  const { currentDocument, createDocument, saveDocument, loadDocument } = useWriting();

  const [settingsOpen, setSettingsOpen] = useState(false);

  const [generationModalOpen, setGenerationModalOpen] = useState(false);

  const handleGenerate = () => {
    setGenerationModalOpen(true);
  };
  return (
    <>

      {/* Generation Dialog */}
      <GenerationModal open={generationModalOpen} setOpen={setGenerationModalOpen} />
      <GPTMenuBar label="Writer" metaOpen={props.metaOpen}>
        <Button variant="contained" onClick={handleGenerate} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          Generators
        </Button>
        {props.mode === "edit" &&
          <Button variant="contained" onClick={() => props.setMode("preview")} sx={{
            marginRight: { xs: '2px', sm: '4px', md: '6px' },
            width: "100px"
          }}>
            Preview
          </Button>}
        {props.mode === "preview" &&
          <Button variant="contained" onClick={() => props.setMode("edit")} sx={{
            marginRight: { xs: '2px', sm: '4px', md: '6px' },
            width: "100px"
          }}>
            Edit
          </Button>}
        <Button variant="contained" onClick={props.metaOpen ? props.handleMetaClose : props.handleMetaOpen} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          TOC
        </Button>

        <Button variant="contained" onClick={() => {
          const title = window.prompt("Enter a title for your document");
          if (!title) {
            return;
          }
          createDocument(title);
        }} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          New
        </Button>
        <Button variant="contained" onClick={() => {
          saveDocument();
        }} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          Save
        </Button>
        <Box sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          <Dropzone onDrop={(acceptedFiles: File[]) => {
            if (acceptedFiles.length === 0)
              return;

            if (acceptedFiles.length > 1) {
              alert("Only one file can be loaded at a time");
              return;
            }
            console.log("Loading", acceptedFiles[0].name, "...")
            loadDocument(acceptedFiles[0]);
          }} />
        </Box>
      </GPTMenuBar>
    </>
  );
}
