import React, { useState, useCallback, useMemo, useEffect, useContext } from 'react';
import { Document, DocumentNode, GeneratorDefinition } from './types';
import { WritingContext } from './context';
import useJSONSaveLoad from '../useSaveLoadJSON';
import { SettingsContext } from '../Settings';
import {
    DEFAULT_STUB_PROMPT, DEFAULT_CONTENT_PROMPT, STUB_ADDITIONAL_PROMPT, EXAMPLE_STUB
} from '../writer/WritingPrompts';
import { Message, iterableStreamGPT4Message } from '../openai_api/gpt4';
import { GenerationType } from './types';
import { parse, stringify } from 'yaml';
import { fileSave } from 'browser-fs-access';
import { useDropzone } from 'react-dropzone';

const BlankNode = (parent?: DocumentNode): DocumentNode => {
    const uuid = crypto.randomUUID();
    return {
        uuid: uuid,
        path: parent ? [...parent.path, uuid] : [uuid],
        name: 'A Modern Love Story',
        stub: EXAMPLE_STUB,
        content: '',
        children: [],
        metadata: {
            generate_node_prompt: "Generate a node prompt",
            generate_child_stubs_prompt: "Generate child stubs prompt",
        }
    }
};

const BlankDocumentWithRoot = (): [Document, DocumentNode] => {
    const uuid = crypto.randomUUID();
    const root = BlankNode();
    const doc = {
        uuid,
        title: 'Book - A Modern Love Story',
        root: root.uuid
    } as Document;
    return [doc, root];
};

// Book generation
const DEFAULT_GENERATORS = {
    // Stub generation
    "acts": {
        uuid: "acts",
        name: "Acts",
        prompt: "Describe a number of acts for this book. Ensure the acts expand on the plot points and events of the book without overlapping with other books within the series.",
        mode: GenerationType.STUBS
    } as GeneratorDefinition,
    "chapters": {
        uuid: "chapters",
        name: "Chapters",
        prompt: "Describe a number of chapters for this act. Ensure the chapters expand on the plot points and events of the act without overlapping with other acts within the book.",
        mode: GenerationType.STUBS
    } as GeneratorDefinition,
    "scenes": {
        uuid: "scenes",
        name: "Scenes",
        prompt: "Describe a number of scenes for this chapter. Ensure the scenes expand on the plot points and events of the chapter without overlapping with other chapters within the act.",
        mode: GenerationType.STUBS
    } as GeneratorDefinition,

    // Content generation
    "book": {
        uuid: "book",
        name: "Book",
        prompt: `
Write a detailed plan for this book, including the following:

Characters
A list of all protagonist(s), antagonist(s), and major/minor characters, including: name, appearance, personality, role, backstory, relationships, and motivations.

Plot
A detailed list of all plot threads, including all major plot points, characters, and themes involved.
`.trim(),
        mode: GenerationType.CONTENT
    } as GeneratorDefinition,
    "act": {
        uuid: "act",
        name: "Act",
        prompt: "Write a detailed plan for this act, including a list of the events for each character, and the plot points",
        mode: GenerationType.CONTENT
    } as GeneratorDefinition,
    "chapter": {
        uuid: "chapter",
        name: "Chapter",
        prompt: "Write a detailed plan for this chapter, including a list of the events for each character, and the plot points",
        mode: GenerationType.CONTENT
    } as GeneratorDefinition,
    "scene": {
        uuid: "scene",
        name: "Scene",
        prompt: "Write this scene in full, as it would appear in the book. Ensure the scene expands on the plot points and events of the scene without overlapping with other parts of the book.",
        mode: GenerationType.CONTENT
    } as GeneratorDefinition,
}

const WritingProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [documents, setDocuments] = useState<{ [key: string]: Document }>({});
    const [nodes, setNodes] = useState<{ [key: string]: DocumentNode }>({});
    const [generators, setGenerators] = useState<{ [key: string]: GeneratorDefinition }>(DEFAULT_GENERATORS);
    const [currentDocument, setCurrentDocument] = useState<Document | null>(null);
    const [currentNode, setCurrentNode] = useState<DocumentNode | null>(null);
    const { model } = useContext(SettingsContext);

    const stateVariables = useMemo(() => ({
        documents, nodes, generators, currentDocument, currentNode
    }), [documents, nodes, generators, currentDocument, currentNode]);

    const createDocument = useCallback((title: string) => {
        const [newDocument, newRoot] = BlankDocumentWithRoot();
        newDocument.title = title;
        setDocuments((documents) => ({ ...documents, [newDocument.uuid]: newDocument }));
        setNodes((nodes) => ({ ...nodes, [newRoot.uuid]: newRoot }));
        setCurrentDocument(newDocument);
        setCurrentNode(newRoot);

        console.log("Created document", newDocument, "with root node", newRoot);
    }, [documents, nodes, setDocuments, setNodes, setCurrentDocument, setCurrentNode]);

    useEffect(() => {
        // Load from local storage if possible
        const stateString = localStorage.getItem('writing')
        if (stateString) {
            const state = parse(stateString);
            setDocuments(state.documents);
            setNodes(state.nodes);
            setGenerators(state.generators);
            setCurrentDocument(state.currentDocument);
            setCurrentNode(state.currentNode);
        }
    }, []);

    // Update the state whenever it changes
    useEffect(() => {
        localStorage.setItem('writing', stringify(stateVariables));
    }, [documents, nodes, generators, currentDocument, currentNode]);

    const editDocument = useCallback((document: Document, edit_fn: (document: Document) => void) => {
        const newDocument = { ...document };
        edit_fn(newDocument);
        setDocuments((documents) => ({ ...documents, [newDocument.uuid]: newDocument }));
    }, [documents, setDocuments]);

    const saveDocument = useCallback(() => {
        // Download as YAML file using fileSave
        const yaml = stringify(stateVariables);
        const blob = new Blob([yaml], { type: 'text/yaml' });
        fileSave(blob);
    }, [documents, nodes, generators, currentDocument, currentNode]);

    const loadDocument = useCallback((file: File) => {
        // Load from YAML file
        const reader = new FileReader();
        reader.onload = (e) => {
            const yaml = e.target?.result as string;
            console.log("Loaded yaml:", yaml);
            const state = parse(yaml);
            console.log("Loaded state:", state);
            setDocuments(state.documents);
            setNodes(state.nodes);
            setGenerators(state.generators);
            setCurrentDocument(state.currentDocument);
            setCurrentNode(state.currentNode);
        };
        reader.readAsText(file);
        console.log("Reading", file, "with", reader);
    }, [documents, nodes, generators, currentDocument, currentNode]);

    const editNode = useCallback((edit_fn: (node: DocumentNode) => void, node?: DocumentNode) => {
        let editing_node = currentNode;
        if (node) {
            editing_node = node;
        }
        if (!editing_node) {
            console.error("No node to edit");
            return;
        }

        const newNode = { ...editing_node };
        edit_fn(newNode);
        setNodes((nodes) => ({ ...nodes, [newNode.uuid]: newNode }));
    }, [currentNode, setNodes, setCurrentNode]);

    const createNode = useCallback((parentUUID: string, properties: Partial<DocumentNode>) => {
        // const newNode = { ...BlankNode(parentNode), ...properties };
        // const newParentNode = { ...parentNode, children: [...parentNode.children, newNode.uuid] };
        // setNodes((nodes) => ({ ...nodes, [newParentNode.uuid]: newParentNode, [newNode.uuid]: newNode }));
        setNodes((nodes) => {
            const parentNode = nodes[parentUUID];
            if (parentNode === undefined) {
                console.error("Could not find parent with uuid " + parentUUID);
                return nodes;
            }
            const newNode = { ...BlankNode(parentNode), ...properties };
            const newParentNode = { ...parentNode, children: [...parentNode.children, newNode.uuid] };
            return { ...nodes, [newParentNode.uuid]: newParentNode, [newNode.uuid]: newNode };
        });
        // return newNode;
    }, [nodes, setNodes]);

    const getParent = useCallback((node: DocumentNode): DocumentNode | null => {
        console.log("Documents:", documents);
        console.log("Nodes:", nodes);
        if (node.path.length === 1) {
            return null;
        }
        return nodes[node.path[node.path.length - 2]];
    }, [nodes]);

    const getSiblings = useCallback((node: DocumentNode): DocumentNode[] => {
        const parent = getParent(node);
        if (parent === null) {
            return [];
        }
        return parent.children.map(uuid => nodes[uuid]);
    }, [nodes]);

    const getChildren = useCallback((node: DocumentNode): DocumentNode[] => {
        return node.children.map(uuid => nodes[uuid]);
    }, [nodes]);

    const getAncestors = useCallback((node: DocumentNode): DocumentNode[] => {
        let cur = nodes[node.path[0]];
        const ancestors: DocumentNode[] = [cur];

        for (const uuid of node.path.slice(1, -1)) {
            cur = nodes[uuid];
            ancestors.push(cur);
        }

        return ancestors;
    }, [nodes]);

    const getDescendants = useCallback((node: DocumentNode): DocumentNode[] => {
        const descendants: DocumentNode[] = [];

        const queue = [...node.children];
        while (queue.length > 0) {
            const uuid = queue.shift();
            if (uuid === undefined) {
                console.error("Queue should not be empty");
                break;
            }
            const child = nodes[uuid];
            if (child === undefined) {
                console.error("Could not find child with uuid " + uuid);
                continue;
            }
            descendants.push(child);
            queue.push(...child.children);
        }

        return descendants;
    }, [nodes]);

    const getFullText = useCallback((node: DocumentNode | null): string | null => {
        // Get the text of this node, and all children (recursively)
        if (!node) {
            return null;
        }
        let text = node.content;
        for (const child of node.children) {
            const childText = getFullText(nodes[child]);
            if (childText) {
                text += "\n\n" + childText;
            }
        }
        return text;
    }, [nodes]);


    const deleteNode = useCallback((node: DocumentNode) => {
        console.log("Deleting node:", node);

        // Remove the node from the parent's children
        setNodes((nodes) => {
            if (node.path.length === 1) {
                console.error("Cannot delete root node");
                return nodes;
            }
            const parent = nodes[node.path[node.path.length - 2]];

            // Create the new parent node
            const newParent = { ...parent, children: parent.children.filter(uuid => uuid !== node.uuid) };

            // Create the new nodes object
            const getDescendants = (node: DocumentNode): DocumentNode[] => {
                const descendants: DocumentNode[] = [];

                const queue = [...node.children];
                while (queue.length > 0) {
                    const uuid = queue.shift();
                    if (uuid === undefined) {
                        console.error("Queue should not be empty");
                        break;
                    }
                    const child = nodes[uuid];
                    if (child === undefined) {
                        console.error("Could not find child with uuid " + uuid);
                        continue;
                    }
                    descendants.push(child);
                    queue.push(...child.children);
                }

                return descendants;
            };

            const nodesToDelete = [node, ...getDescendants(node)];

            // Remove the nodes to delete from the new nodes object
            const newNodes = { ...nodes };
            for (const node of nodesToDelete) {
                delete newNodes[node.uuid];
            }

            // Add the new parent to the new nodes object
            newNodes[newParent.uuid] = newParent;

            return newNodes;
        });
    }, [nodes, setNodes]);

    // const generate = useCallback(async (cur: DocumentNode, generationMode: GenerationType, contentPrompt: string, stubPrompt: string) => {
    const generate = useCallback(async (cur: DocumentNode, generator: GeneratorDefinition) => {
        const name = cur?.name;

        if (!cur) {
            console.error("Current node is null");
            return;
        }

        // Get the full text of the current node
        const fullText = getFullText(cur);
        const siblings = getSiblings(cur);

        const context: string[] = [];
        if (fullText) {
            context.push("* Current Content of the Node *");
            context.push(fullText);
        }

        context.push("* Content of Current Node's Children *");
        context.push(cur.children.map(c => nodes[c].stub).join("\n"));

        context.push("* Content of Current Node's Siblings *");
        context.push(siblings.map(s => s.stub).join("\n"));

        context.push("* Current Node's Ancestry *");
        context.push(getAncestors(cur).slice(0, -1).map(a => {
            return "* " + a.name + "\n" + a.stub + "\n";
        }).join("\n"));

        context.push("* Current Node Children Stubs *");
        context.push(cur.children.map(c => nodes[c].stub).join("\n"));

        context.push("Please enter the content of the node below:");

        // Generate the text
        switch (generator.mode) {
            case "content":
                generateContent();
                break;
            case "stubs":
                generateStubs();
                break;
            default:
                console.error("Unknown generation mode " + generator.mode);
                return;
        }

        async function generateContent() {
            const instructions = generator.prompt;
            const prompt = [
                "You are generating a node based on the following information:",
                "Name: " + name,
                "Stub: " + cur.stub,
                "",
                "* Context Information *" + context.join("\n"),
            ];
            const promptText = prompt.join("\n");

            // Create the messages to GPT-4
            const messages = [
                { role: "system", content: instructions },
                { role: "user", content: promptText },
            ] as Message[];

            // Send the messages to GPT-4 and parse the response from iterableStreamGPT4Message
            for await (const { token, whole, done } of iterableStreamGPT4Message(messages, model)) {
                // Set the content
                editNode((node: DocumentNode) => {
                    node.content = whole;
                });
            }
        }

        async function generateStubs() {
            const instructions = generator.prompt + "\n\n" + STUB_ADDITIONAL_PROMPT;
            const prompt = [
                "You are generating a number of child objects based on the following information:",
                "Name: " + name,
                "Stub: " + cur.stub,
                "",
                "* Context Information *" + context.join("\n"),
                "",
                STUB_ADDITIONAL_PROMPT
            ];
            const promptText = prompt.join("\n");

            // Create the messages to GPT-4
            const messages = [
                { role: "system", content: instructions },
                { role: "user", content: promptText },
            ] as Message[];

            // Send the messages to GPT-4 and parse the response from iterableStreamGPT4Message
            let acc = "";
            for await (const { token, whole, done } of iterableStreamGPT4Message(messages, model)) {
                console.log(whole);
                acc += token;

                const addNode = (stublines: string) => {
                    const [name, ...stubLines] = stublines.split("\n").filter(l => l.trim() !== "");
                    const stubText = stubLines.join("\n");
                    createNode(cur.uuid, {
                        name, stub: stubText
                    });
                };
                while (acc.includes("\n\n")) {
                    const [stub, ...rest] = acc.split("\n\n");
                    acc = rest.join("\n\n");
                    addNode(stub);
                }

                // Push the last bit of text
                if (done && acc.trim() !== "") {
                    addNode(acc);
                }
            }
        }
    }, [getFullText, getAncestors, getSiblings, createNode, editNode, nodes, model]);

    const createGenerator = useCallback(() => {
        const uuid = crypto.randomUUID();
        const generator = {
            uuid,
            name: "New Generator",
            mode: "content",
            prompt: "Please enter the content of the node below:",
        } as GeneratorDefinition;

        setGenerators((generators) => {
            return {
                ...generators,
                [generator.uuid]: generator,
            };
        })
    }, []);

    const deleteGenerator = useCallback((generator: GeneratorDefinition) => {
        setGenerators((generators) => {
            const newGenerators = { ...generators };
            delete newGenerators[generator.uuid];
            return newGenerators;
        });
    }, []);

    const editGenerator = useCallback((generator: GeneratorDefinition, edit_fn: (generator: GeneratorDefinition) => void) => {
        setGenerators((generators) => {
            const newGenerators = { ...generators };
            edit_fn(newGenerators[generator.uuid]);
            return newGenerators;
        });
    }, []);

    // Find the parent node (second-last element of the path)
    return (
        <WritingContext.Provider value={{
            ...stateVariables,
            createDocument,
            editDocument,
            saveDocument,
            loadDocument,
            createNode,
            editNode,
            deleteNode,
            setCurrentDocument,
            setCurrentNode,
            tools: {
                getParent,
                getSiblings,
                getChildren,
                getAncestors,
                getFullText,
            },
            generate, createGenerator, editGenerator, deleteGenerator,
        }}>
            {children}
        </WritingContext.Provider>
    );
}

export default WritingProvider;