import { ConversationState, DialogRoot, DialogNode } from './context';

// Helper function to open the IndexedDB
const openIndexedDB = async (): Promise<IDBDatabase> => {
    return new Promise((resolve, reject) => {
        const request = indexedDB.open("ConversationDB", 1);

        request.onupgradeneeded = (event: IDBVersionChangeEvent) => {
            const db = (event.target as IDBOpenDBRequest).result;
            db.createObjectStore("roots");
            db.createObjectStore("nodes");
        };

        request.onsuccess = (event: Event) => {
            resolve((event.target as IDBOpenDBRequest).result);
        };

        request.onerror = (event: Event) => {
            reject((event.target as IDBOpenDBRequest).error);
        };
    });
};

// Helper function to load roots from IndexedDB
export const loadRoots = async (): Promise<DialogRoot[]> => {
    const db = await openIndexedDB();
    const transaction = db.transaction("roots", "readonly");
    const rootsStore = transaction.objectStore("roots");
    const request = rootsStore.getAll();

    return new Promise((resolve, reject) => {
        request.onsuccess = (event: Event) => {
            resolve((event.target as IDBRequest<DialogRoot[]>).result);
        };

        request.onerror = (event: Event) => {
            reject((event.target as IDBRequest).error);
        };
    });
}

// Helper function to load nodes from IndexedDB
export const loadNodes = async (): Promise<DialogNode[]> => {
    const db = await openIndexedDB();
    const transaction = db.transaction("nodes", "readonly");
    const nodesStore = transaction.objectStore("nodes");
    const request = nodesStore.getAll();

    return new Promise((resolve, reject) => {
        request.onsuccess = (event: Event) => {
            resolve((event.target as IDBRequest<DialogNode[]>).result);
        };

        request.onerror = (event: Event) => {
            reject((event.target as IDBRequest).error);
        };
    });
}

export const reconcileNodes = async (updatedNodes: DialogNode[], deletedNodes: string[]): Promise<void> => {
    // Update/delete nodes (create = update)
    const db = await openIndexedDB();
    const transaction = db.transaction("nodes", "readwrite");
    const nodesStore = transaction.objectStore("nodes");

    for (const node of updatedNodes) {
        nodesStore.put(node, node.uuid);
    }

    for (const uuid of deletedNodes) {
        nodesStore.delete(uuid);
    }
}

export const reconcileRoots = async (updatedRoots: DialogRoot[], deletedRoots: string[]): Promise<void> => {
    // Update/delete roots (create = update)
    const db = await openIndexedDB();
    const transaction = db.transaction("roots", "readwrite");
    const rootsStore = transaction.objectStore("roots");

    for (const root of updatedRoots) {
        console.log("Saving root to IndexedDB:", root);
        rootsStore.put(root, root.uuid);
    }

    for (const uuid of deletedRoots) {
        console.log("Deleting node with uuid:", uuid)
        rootsStore.delete(uuid);
    }
}

export const reconcileState = async (state: ConversationState): Promise<void> => {
    // Delete all nodes and roots that are not in the state
    const allNodes = await loadNodes();
    const allRoots = await loadRoots();

    // Update nodes and roots in db
    const db = await openIndexedDB();
    const transaction = db.transaction(["nodes", "roots"], "readwrite");
    const nodesStore = transaction.objectStore("nodes");
    const rootsStore = transaction.objectStore("roots");

    // Add or update all requested nodes
    for (const node of Object.values(state.nodes)) {
        nodesStore.put(node, node.uuid);
    }

    // Add or update all requested roots
    for (const root of Object.values(state.roots)) {
        rootsStore.put(root, root.uuid);
    }

    // for (const node of allNodes) {
    //     if (!state.nodes[node.uuid]) {
    //         nodesStore.delete(node.uuid);
    //     }
    // }

    // for (const root of allRoots) {
    //     if (!state.roots[root.uuid]) {
    //         rootsStore.delete(root.uuid);
    //     }
    // }

    for (const trashNode of state.trash) {
        nodesStore.delete(trashNode);
        rootsStore.delete(trashNode);
    }
}
