import React, { useState, useCallback, useEffect } from 'react';
import { DialogNode, useConversation } from '../conversation/context';
import { useSettings } from '../Settings';
import { Button } from '@mui/material';
import { useEditor, EditorContent } from '@tiptap/react'
import StarterKit from '@tiptap/starter-kit'
import TurndownService from 'turndown';

import './markdown.scss';

export function ChatInput() {
    const settings = useSettings();
    const { state, dispatch, actions } = useConversation();
    const { userResponse } = actions;
    const [message, setMessage] = useState('');

    const editor = useEditor({
        extensions: [
            StarterKit,
        ],
        content: message,
    })

    useEffect(() => {
        if (!editor) {
            return;
        }

        // Reference: https://github.com/ueberdosis/tiptap/issues/2403#issuecomment-1017840603
        editor.off("update");
        editor.on("update", ({ editor: updatedEditor }) => {
            setMessage(updatedEditor.getHTML());
        });
    }, [editor]);

    // When the message changes, update the editor
    useEffect(() => {
        if (!editor) {
            return;
        }

        if (editor.getHTML() !== message) {
            editor.commands.setContent(message);
        }
    }, [message, editor]);

    const currentNode = state.nodes[state.currentNode];

    const handleClick = useCallback(() => {
        // Convert from HTML to Markdown
        const html = message;
        console.log("HTML:", html);
        const markdown = new TurndownService().turndown(html);
        console.log("Markdown:", markdown);
        userResponse(currentNode.uuid, markdown.trim(), 'user');
        setMessage('');
        const chatWindow = document.getElementById('chat-window');
        if (chatWindow) {
            setTimeout(() => {
                chatWindow.scrollTop = chatWindow.scrollHeight;
            }, 100);
        }
    }, [message, currentNode, userResponse]);

    if (!settings.openaiApiKey) {
        return (
            <div style={{
                paddingTop: "12px",
                display: "flex",
                justifyContent: "center",
                alignItems: "center",
                textAlign: "center",
            }}>
                <h3>
                    Welcome! Please set your OpenAI API key in the settings panel (top-right corner).
                </h3>
            </div>
        );
    }

    return (
        <div style={{
            height: settings.warn ? "194px" : '174px',
            width: "100%",
        }}
        >
            <EditorContent
                editor={editor}
                className="input"
                style={{
                    height: settings.warn ? "102px" : "122px",
                }}
                onKeyDown={(e) => {
                    if ((e.ctrlKey || e.metaKey) && e.key === 'Enter') {
                        handleClick();
                    }
                }}
            />
            <div style={{ height: "8px" }} />
            {settings.warn ? (
                <div style={{
                    height: "20px",
                    fontSize: '14px',
                    color: 'black',
                    // Center text in div
                    display: 'flex',
                    alignItems: 'center',
                    justifyContent: 'center',
                    marginTop: "-6px",
                    paddingTop: "3px",
                    paddingBottom: "3px",
                }}>
                    <i>AI systems give inaccurate information and are prone to bias. You use this app entirely at your own risk.</i>
                </div>
            ) : null}
            <Button onClick={handleClick}
                style={{
                    padding: "0px",
                    marginTop: "0px",
                    width: "100%",
                    height: "30px",
                    color: "#f5f5f5"
                }}
                disabled={!(state.currentRoot && state.currentNode)}
            >
                Send
            </Button>
        </div>
    );
}