import ReactMarkdown from 'react-markdown';
// import Menu from '@mui/material/Menu';
// import MenuItem from '@mui/material/MenuItem';
import { Message, iterableStreamGPT4Message } from '../openai_api/gpt4';
import { useSettings } from '../Settings';
import { MenuBar } from './WriterMenuBar';
import { EXAMPLE_STUB, DEFAULT_CONTENT_PROMPT, DEFAULT_STUB_PROMPT, STUB_ADDITIONAL_PROMPT } from './WritingPrompts';
import { DocumentNode, GenerationType, GeneratorDefinition, useWriting } from '../writing';
