import RenameIcon from '@mui/icons-material/Edit';
import DeleteIcon from '@mui/icons-material/Delete';
import GenerateIcon from '@mui/icons-material/PlayArrow';
import { ContextMenu } from './ContextMenu';

function Writer() {
  const { setCurrentNode } = useWriting();

  const [mode, setMode] = useState<"edit" | "preview">("edit");
  const [metaOpen, setMetaOpen] = useState(true);
  const handleMetaOpen = () => setMetaOpen(true);
  const handleMetaClose = () => setMetaOpen(false);

  return (
    <Box
      style={{
        width: "100vw",
        height: "calc(100vh - 64px)",
        padding: "0px",
        margin: "0px"
      }}
      sx={{
        "& .MuiButton-root": {
          backgroundColor: "#3c3c3c",
          color: "white",
          "&:hover": {
            backgroundColor: "#5c5c5c",
            color: "white",
          }
        },
      }}
    >
      <MenuBar metaOpen={metaOpen} handleMetaOpen={handleMetaOpen} handleMetaClose={handleMetaClose} mode={mode} setMode={setMode} />
      <div style={{
        height: "calc(100% + 16px)",
        width: "100%",
      }}>
        {/* <DualPanel
          main={<WritePanel mode={mode} />}
          left={<TOCPanel
            mode={mode}
            select={(node: DocumentNode) => {
              setCurrentNode(node);
            }}
          />}
          detailViewOpen={metaOpen}
          closeButton={true}
        /> */}
      </div>
    </Box>
  );
}

function WritePanel(props: { mode: "edit" | "preview" }) {
  const { editNode, currentNode, tools } = useWriting();
  if (!currentNode) {
    return null;
  }
  return (
    <div style={{
      backgroundColor: '#b0b0b0',
      overflow: 'none',
      height: '-webkit-fill-available',
      width: "-webkit-fill-available",
    }}>
      {
        props.mode === "edit" && (
          <div style={{ height: "100%", width: "100%" }}>
            <div style={{ height: "calc(100% - 260px)" }}>
              <Editor
                // Word wrap
                options={{
                  wordWrap: "on",
                  wrappingStrategy: "advanced"
                }}
                onChange={(value) => editNode((node: DocumentNode) => {
                  node.content = value || "";
                })}
                defaultLanguage="markdown"
                value={currentNode.content}
                theme="vs-dark"
              />
            </div>
            <div style={{ height: "10px", background: "#191919" }} />
            <div style={{ height: "250px" }}>
              <Editor
                // Word wrap
                options={{
                  wordWrap: "on",
                  // wrappingIndent: "indent",
                  wrappingStrategy: "advanced",
                  scrollBeyondLastLine: false,
                }}
                onChange={(value) => editNode((node: DocumentNode) => {
                  node.stub = value || "";
                })}
                defaultLanguage="markdown"
                value={currentNode?.stub}
                theme="vs-dark"
              />
            </div>
          </div>
        )
      }
      {
        props.mode === "preview" && (
          <div style={{ padding: "0px", margin: "0px", marginTop: "-15px" }}>
            <ReactMarkdown>
              {tools.getFullText(currentNode) || ""}
            </ReactMarkdown>
          </div>
        )
      }
    </div>
  )
}

function TOCPanel(props: { mode: "edit" | "preview", select: (node: DocumentNode) => void }) {
  const { createNode, editNode, deleteNode, currentDocument, currentNode, nodes, tools, generate } = useWriting();
  const [selected, setSelected] = useState<string[]>([]);

  const handleStubChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    const value = event.target.value;
    // Set the stub of the current node
    editNode((node: DocumentNode) => {
      node.stub = value;
    });
  };

  const create = (node: DocumentNode) => {
    const name = window.prompt("Enter a name for this node");
    if (!name) return;
    createNode(node.uuid, {
      name
    });
  };

  const rename = (node: DocumentNode) => {
    editNode((node: DocumentNode) => {
      node.name = window.prompt("Enter a new name for this node", node.name) || node.name;
    }, node);
  };

  const _delete = (node: DocumentNode) => {
    // Check first
    deleteNode(node);
  };

  const handleCreateNode = (node: DocumentNode) => {
    if (selected.length < 2) {
      create(node);
      return;
    }
    selected.map((uuid) => {
      create(nodes[uuid]);
    });
  }

  const handleRenameNode = (node: DocumentNode) => {
    if (selected.length < 2) {
      rename(node);
      return;
    }

    selected.map((uuid) => {
      rename(nodes[uuid]);
    });
  }

  const handleDeleteNode = (node: DocumentNode) => {
    if (!window.confirm("Are you sure you want to delete these nodes?")) {
      return;
    }

    if (selected.length < 2) {
      _delete(node);
      return;
    }
    selected.map((uuid) => {
      _delete(nodes[uuid]);
    });
  }

  const generateNode = (generator: GeneratorDefinition) => {
    selected.map((uuid) => {
      generate(nodes[uuid], generator);
    });
  }

  return (
    <div style={{
      // Expand to fill parent
      height: "100%",
      width: "300px",
      paddingTop: "10px",
      backgroundColor: "#191919",
      color: "#d4d4d4",
      display: "flex",
      flexDirection: "column",
      padding: "0px",
      margin: "0px",
      marginRight: "-15px",
      marginBottom: "-15px",
      overflowY: "auto",
      overflowX: "hidden",
    }}>
      <TreeView
        style={{ width: "285px" }}
        defaultCollapseIcon={<ExpandMore />}
        defaultExpandIcon={<ChevronRight />}
        multiSelect={true}
        onNodeSelect={(event, nodeIds) => {
          setSelected(nodeIds);
        }}
      >
        {
          currentDocument ? (
            <TOCNode node={nodes[currentDocument.root]} select={props.select} handlers={{
              CreateNode: handleCreateNode,
              RenameNode: handleRenameNode,
              DeleteNode: handleDeleteNode,
              GenerateNode: generateNode,
            }} />
          ) : (
            <span>
              No document open
            </span>
          )
        }
      </TreeView>
    </div>
  )
}

function TOCNode(props: {
  node: DocumentNode,
  select: (node: DocumentNode) => void,
  handlers: {
    CreateNode: (node: DocumentNode) => void,
    RenameNode: (node: DocumentNode) => void,
    DeleteNode: (node: DocumentNode) => void,
    GenerateNode: (generator: GeneratorDefinition, node: DocumentNode) => void;
  }
}) {
  const { nodes } = useWriting();

  return (
    <div>
      <TreeItem
        nodeId={props.node.uuid}
        label={<TOCLabel {...props} />}
        onClick={() => props.select(props.node)}
      >
        {props.node.children.map((child, index) => {
          const childNode = nodes[child];
          return (
            <TOCNode node={childNode} select={props.select} key={index} handlers={props.handlers} />
          )
        })}
      </TreeItem>
    </div >
  )
}

function TOCLabel(props: {
  node: DocumentNode;
  select: (node: DocumentNode) => void;
  handlers: {
    CreateNode: (node: DocumentNode) => void;
    RenameNode: (node: DocumentNode) => void;
    DeleteNode: (node: DocumentNode) => void;
    GenerateNode: (generator: GeneratorDefinition, node: DocumentNode) => void;
  };
}): JSX.Element {
  const { generators, generate } = useWriting();
  const ref = useRef<HTMLDivElement>(null);
  const [menuOpen, setMenuOpen] = useState(false);

  const stubGenerators = useMemo(() => {
    return Object.values(generators).filter((generator) => generator.mode === GenerationType.STUBS);
  }, [generators]);

  const contentGenerators = useMemo(() => {
    return Object.values(generators).filter((generator) => generator.mode === GenerationType.CONTENT);
  }, [generators]);

  const handleContextMenu = (event: React.MouseEvent<HTMLDivElement, MouseEvent>) => {
    event.preventDefault();
    event.stopPropagation();
    setMenuOpen(true);
  };
  return (
    <>
      <div ref={ref} style={{ width: "100%", height: "100%" }} onContextMenu={handleContextMenu}>
        {props.node.name}
      </div>
      {
        menuOpen &&
        <ContextMenu
          nodeTypes={[
            { key: "Create", title: "Create", label: "Create" },
            { key: "Rename", title: "Rename", label: "Rename" },
            { key: "Delete", title: "Delete", label: "Delete" },
            {
              key: "Stubs", title: "Stubs", label: "Generate Stubs", children: stubGenerators.map((generator) => {
                return {
                  key: `Stubs/${generator.uuid}`,
                  title: generator.name,
                  label: generator.name,
                }
              })
            },
            {
              key: "Content", title: "Content", label: "Generate Content", children: contentGenerators.map((generator) => {
                return {
                  key: `Content/${generator.uuid}`,
                  title: generator.name,
                  label: generator.name,
                }
              })
            },
          ]}
          onSelect={(key) => {
            console.log("Selected key:", key);
            switch (key) {
              case "Create":
                props.handlers.CreateNode(props.node);
                return;
              case "Rename":
                props.handlers.RenameNode(props.node);
                return;
              case "Delete":
                props.handlers.DeleteNode(props.node);
                return;
            }

            const [type, uuid] = key.split("/");
            const generator = generators[uuid];
            if (!generator) return;
            props.handlers.GenerateNode(generator, props.node);
          }}
          handleClose={() => { setMenuOpen(false) }}
          enabled={true}
          anchorEl={ref.current}
          width={160}
          height={120}
        />
      }
    </>
  );
}

export default Writer;
