import React, { useState, useCallback } from 'react';
// import Teacher from './teacher/Teacher'
import { DualPanel } from './DualPanel';
import { ChatWindow } from './teacher/ChatWindow';
import MetaView from './metaview/MetaView';
import { Button } from '@mui/material';
import { useConversation } from './conversation/context';
import { GPTMenuBar } from './GPTMenuBar';
// Imports for MUI help modal
import { Dialog, DialogTitle, DialogContent, DialogContentText, DialogActions } from '@mui/material';
import { HelpOutline } from '@mui/icons-material';
import "./Discussion.css";

function Discussion() {
  const [metaOpen, setMetaOpen] = useState(false);

  const handleMetaOpen = () => setMetaOpen(true);
  const handleMetaClose = () => setMetaOpen(false);

  return (
    <>
      <MenuBar handleMetaOpen={handleMetaOpen} handleMetaClose={handleMetaClose} metaOpen={metaOpen} />
      <DualPanel
        main={(props) => (<ChatWindow {...props} />)}
        left={(props) => (<MetaView {...props} />)}
        detailViewOpen={metaOpen}
        closeButton={true}
        onClose={handleMetaClose}
      />
    </>
  );
}

function HelpModal(props: { open: boolean, handleClose: () => void }) {
  return (
    <Dialog
      open={props.open}
      onClose={props.handleClose}
      aria-labelledby="help-dialog-title"
      aria-describedby="help-dialog-description"
    >
      <DialogTitle id="help-dialog-title">AssistantGPT Help</DialogTitle>
      <DialogContent>
        <DialogContentText id="help-dialog-description">
          <b>What is AssistantGPT?</b>
          <p>AssistantGPT is an interface for GPT-3.5 and GPT-4 that's designed to be as simple and frictionless as possible.</p>
          <b>Where is my data stored?</b>
          <p>AssistantGPT is entirely local, which means there's no back-end server storing any data. This is great for privacy, but it also means that if you clear your history you'll lose your data - so be careful!</p>
          <b>How do I get started?</b>
          <p>Set your OpenAI API key in Settings, and choose your preferred model. Then click the "Chats" button in the menu bar to open the Chat picker, and create a new root chat.</p>
          <b>Hints</b>
          <ul>
            <li>Hold shift while clicking a message to open all the way to the next message with more than one child.</li>
            <li>You can save a chat by opening the relevant dialog and using Control-P (or Command-P on a Mac) to print to a PDF. The PDF will be nicely formatted!</li>
            <li>There are surely bugs, so let me know if you find any!</li>
          </ul>
        </DialogContentText>
      </DialogContent>
      <DialogActions>
        <Button onClick={props.handleClose}>Close</Button>
      </DialogActions>
    </Dialog>
  );
}

function MenuBar(props: { handleMetaOpen: () => void, handleMetaClose: () => void, metaOpen: boolean }) {
  // const { saveToFile, loadFromFile, resetConversation } = useConversation();
  const { state, dispatch, actions } = useConversation();
  const [helpOpen, setHelpOpen] = useState(false);

  const handleHelpOpen = () => setHelpOpen(true);
  const handleHelpClose = () => setHelpOpen(false);
  const [settingsOpen, setSettingsOpen] = useState(false);
  return (
    <>
      <GPTMenuBar label="Assistant" metaOpen={props.metaOpen}>
        <Button variant="contained" onClick={props.metaOpen ? props.handleMetaClose : props.handleMetaOpen} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          Chats
        </Button>
        <Button variant="contained" onClick={handleHelpOpen} sx={{
          marginRight: { xs: '2px', sm: '4px', md: '6px' },
        }}>
          Help
        </Button>
      </GPTMenuBar >
      <HelpModal open={helpOpen} handleClose={handleHelpClose} />
    </>
  );
}

export default Discussion;