import React, { useMemo } from 'react';
import { DialogRoot, useConversation } from '../conversation/context';
import { Button } from '@mui/material';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemButton from '@mui/material/ListItemButton';
import CloseIcon from '@mui/icons-material/Close';
import ContentEditable from 'react-contenteditable'
import './metaview.scss';

export default function MetaView(props: { maxWidth: number }) {
    // const { roots, createBlankRoot, currentRoot } = useConversation();
    const { state, dispatch } = useConversation();

    const widthLimit = useMemo(() => {
        return props.maxWidth + "px";
    }, [props.maxWidth]);

    if (!state.roots) {
        return null;
    }

    return (
        <div style={{
            minWidth: "300px",
            maxWidth: widthLimit,
            height: "calc(100% - 88px)",
            backgroundColor: "#f0f0f0",
            // Show items flexed vertically
            display: "flex",
            paddingLeft: "4px",
            paddingRight: "4px",
            flexDirection: "column",
        }}>
            {/* roots is like { [key: string]: DialogRoot }*/}
            <Button style={{
                flex: "none",
                height: "30px",
                width: "100%",
                marginTop: "8px",
                marginBottom: "8px"
            }} onClick={() => {
                dispatch({
                    type: "create-root",
                    uuid: crypto.randomUUID(),
                })
            }}>
                Create new root
            </Button>

            <div style={{
                flex: "1 1 auto",
                maxHeight: "calc(100% - 32px)",
                overflow: "auto" // Move this style here
            }}>
                <List style={{
                    // Take up all available space
                }}>
                    <div>
                        {Object.values(state.roots).map(root => (
                            <RootItem key={root.uuid} root={root} />
                        ))}
                    </div>
                </List>
            </div>
        </div >
    )
}

function RootItem(props: { root: DialogRoot }) {
    // const { currentRoot, setCurrentRoot, deleteRoot, setRootName } = useConversation();
    const { state, dispatch } = useConversation();

    const handleDelete = (e: React.MouseEvent) => {
        // Don't pass through
        e.stopPropagation();

        // Delete the root
        dispatch({
            type: "delete-root",
            uuid: props.root.uuid,
        });
    };

    return (
        <ListItem disablePadding style={{
            // If this is the current root, highlight it
            backgroundColor: state.currentRoot === props.root.uuid ? "#b0b0b0" : undefined,
            borderRadius: "6px",
        }}>
            <ListItemButton onClick={() => {
                if (state.currentRoot !== props.root.uuid) {
                    dispatch({
                        type: "set-current-root",
                        uuid: props.root.uuid,
                    });
                }
            }}>
                <div style={{
                    // Show in the top-right corner of the button
                    position: "absolute",
                    top: "16px",
                    right: "4px",
                    // Make sure it's on top of the button
                    zIndex: 1,
                }}>
                    {/* <EditIcon
                        style={{ padding: "0px" }}
                        onClick={() => setIsEditing(!isEditing)}
                    /> */}
                    <CloseIcon
                        style={{ padding: "0px" }}
                        onClick={handleDelete}
                    />
                </div>
                {/* <ListItemText primary={props.root.name} secondary={props.root.summary} /> */}
                <div style={{
                    width: "100%",
                    marginLeft: "-8px",
                }}>
                    <ContentEditable
                        // onClick={(e) => {
                        //     // Don't pass through
                        //     e.stopPropagation();
                        // }}
                        tagName="p"
                        style={{
                            padding: "0px",
                            margin: "0px",
                            fontWeight: "bold",
                            fontSize: "1.2em",
                            width: "100%"
                        }}
                        html={props.root.name}
                        onChange={(e) => {
                            dispatch({
                                type: "update-root",
                                uuid: props.root.uuid,
                                updateFn: (root) => {
                                    root.name = e.target.value;
                                    return root;
                                }
                            });
                        }}
                    />
                    <ContentEditable
                        // onClick={(e) => {
                        //     // Don't pass through
                        //     e.stopPropagation();
                        // }}
                        tagName='p'
                        style={{
                            padding: "0px",
                            margin: "0px",
                            width: "100%"
                        }}
                        html={props.root.summary}
                        onChange={(e) => {
                            dispatch({
                                type: "update-root",
                                uuid: props.root.uuid,
                                updateFn: (root) => {
                                    root.summary = e.target.value;
                                    return root;
                                }
                            });
                        }}
                    />
                </div>
            </ListItemButton >
        </ListItem >
    );
}
