/*
The Navigator component shows a set of items, taking the following as inputs:
 - items: The items to show
 - itemRenderer: Renders the item (in the overview list)
 - detailRenderer: Renders the item (in the detail view)

On desktop, the overview list is shown on the left, and the detail view is shown on the right.
On mobile, the detail view is a modal that is shown on top of the overview list.

*/

import React, { useState, useMemo, useEffect } from "react";
import Box from '@mui/material/Box';
import Stack from '@mui/material/Stack';
import Modal from '@mui/material/Modal';
import Button from '@mui/material/Button';
import CloseIcon from '@mui/icons-material/Close';
import useMediaQuery from '@mui/material/useMediaQuery';

const MOBILE_WIDTH = 800;
const DESKTOP_WIDTH = 300;

const useMobileMode = () => {
    const [mobileMode, setMobileMode] = useState(false);
    const matches = useMediaQuery('(max-width:' + MOBILE_WIDTH + 'px)');
    useEffect(() => {
        setMobileMode(matches);
    }, [matches]);
    return mobileMode;
}

export const DualPanel = (props: {
    // main: JSX.Element,
    // left?: JSX.Element,
    // right?: JSX.Element,
    main: (props: {}) => JSX.Element,
    left?: (props: { maxWidth: number }) => JSX.Element,
    right?: (props: { maxWidth: number }) => JSX.Element,
    closeButton?: boolean,
    detailViewOpen?: boolean,
    onClose?: () => void,
}) => {
    const mobileMode = useMobileMode();
    const [detailViewOpen, setDetailViewOpen] = useState(props.detailViewOpen ?? false);
    useEffect(() => {
        setDetailViewOpen(props.detailViewOpen ?? false);
    }, [props.detailViewOpen]);

    const detail = useMemo(() => {
        if (detailViewOpen) {
            return (
                <div>
                    <MobileModal
                        open={detailViewOpen}
                        // handleClose={() => setDetailViewOpen(false)}
                        handleClose={() => {
                            setDetailViewOpen(false);
                            if (props.onClose) {
                                props.onClose();
                            }
                        }}
                        closeButton={props.closeButton}
                    >
                        <>
                            {/* {props.left}
                            {props.right} */}
                            {props.left && props.left({
                                maxWidth: (mobileMode ? MOBILE_WIDTH : DESKTOP_WIDTH)
                            })}
                            {props.right && props.right({
                                maxWidth: (mobileMode ? MOBILE_WIDTH : DESKTOP_WIDTH)
                            })}
                        </>
                    </MobileModal>
                </div>
            );
        }
        return null;
    }, [props, detailViewOpen, mobileMode]);

    const mainWidth = useMemo(() => {
        return (mobileMode || !detailViewOpen) ? "100%" : "calc(100% - " + (DESKTOP_WIDTH) + "px)";
    }, [detailViewOpen, mobileMode]);
    console.log("Main width:", mainWidth)
    return (
        <div style={{
            // Flex row
            display: "flex",
            flexDirection: "row",
        }}>
            {props.left && detail}
            <div style={{ height: "100%", width: mainWidth }}>
                {/* {props.main} */}
                {props.main({ detailViewOpen })}
            </div>
            {!props.left && detail}
        </div>
    );
}

export function MobileModal(props: { open: boolean, handleClose: () => void, children: JSX.Element, closeButton?: boolean }) {
    const mobileMode = useMobileMode();
    console.log("Mobile mode:", mobileMode);
    const open = useMemo(() => {
        return props.open;
    }, [props]);

    if (!mobileMode) {
        return (
            <>{props.children}</>
        );
    }
    return (
        <Modal
            open={open}
            onClose={props.handleClose}
            aria-labelledby="modal-modal-title"
            aria-describedby="modal-modal-description"
        >
            <div
                style={{
                    display: "flex",
                    width: "100vw",
                    height: "100vh",
                    cursor: "pointer",
                }}
                onClick={() => {
                    if (props.closeButton) {
                        props.handleClose();
                    }
                }}
            >
                <div style={{
                    width: "100%",
                    height: "100%",
                    margin: "30px",
                    overflow: "none",
                    cursor: "auto",
                }}>
                    {props.children}
                    {/* Close button in top-right corner */}
                    {props.closeButton && (<button
                        color="primary"
                        onClick={props.handleClose}
                        style={{
                            position: "absolute",
                            top: 5,
                            right: 0,
                            backgroundColor: "transparent",
                            border: "none",
                            boxShadow: "none",
                            color: "white",
                            cursor: "pointer",
                        }}
                    >
                        <CloseIcon />
                    </button>
                    )}
                </div>
            </div >
        </Modal >
    );
}