import React, { useCallback, useState } from 'react';
import { Button, Divider } from '@mui/material';
import Dialog from '@mui/material/Dialog';
import DialogTitle from '@mui/material/DialogTitle';
import DialogContent from '@mui/material/DialogContent';
import DialogActions from '@mui/material/DialogActions';
import List from '@mui/material/List';
import ListItem from '@mui/material/ListItem';
import ListItemText from '@mui/material/ListItemText';
import TextField from '@mui/material/TextField';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import Radio from '@mui/material/Radio';
import { GenerationType, GeneratorDefinition, useWriting } from '../writing';
import { DualPanel } from '../DualPanel';

// function generationModal(generationModalOpen: boolean, setGenerationModalOpen: React.Dispatch<React.SetStateAction<boolean>>, generationMode: string, handleGenerationModeChange: (event: React.ChangeEvent<HTMLInputElement>) => void, numberOfStubs: string, setNumberOfStubs: React.Dispatch<React.SetStateAction<"auto" | "fixed" | "range">>, fixedValue: number, setFixedValue: React.Dispatch<React.SetStateAction<number>>, rangeMin: number, setRangeMin: React.Dispatch<React.SetStateAction<number>>, rangeMax: number, setRangeMax: React.Dispatch<React.SetStateAction<number>>, stubPrompt: string, handleStubPromptChange: (event: React.ChangeEvent<HTMLInputElement>) => void, contentPrompt: string, handleContentPromptChange: (event: React.ChangeEvent<HTMLInputElement>) => void, currentNode: DocumentNode | null, handleStubChange: (event: React.ChangeEvent<HTMLInputElement>) => void, generate: (cur: DocumentNode, generator: GeneratorDefinition) => Promise<void>) {
export interface GenerationModalProps {
  open: boolean;
  setOpen: React.Dispatch<React.SetStateAction<boolean>>;
}

export function GenerationModal({ open, setOpen }: GenerationModalProps) {
  const [currentGenerator, setCurrentGenerator] = useState<GeneratorDefinition | null>(null);

  return (
    <Dialog open={open} onClose={() => setOpen(false)} maxWidth={false}>
      <DialogTitle>Generator Designer</DialogTitle>
      <DialogContent>
        <div style={{
          height: "60vh",
          width: "80vw"
        }}>
          {/* <DualPanel
            main={<GeneratorDesigner cur={currentGenerator} />}
            left={<GeneratorList cur={currentGenerator} setCur={setCurrentGenerator} />}
            detailViewOpen={true}
            closeButton={true}
          /> */}
        </div>
      </DialogContent>
      <DialogActions>
        <Button onClick={() => setOpen(false)}>Close</Button>
      </DialogActions>
    </Dialog>
  )
}

function GeneratorDesigner({ cur }: { cur: GeneratorDefinition | null }) {
  const { editGenerator, deleteGenerator } = useWriting();

  const setName = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    if (!cur) return;

    editGenerator(cur, (generator: GeneratorDefinition) => {
      generator.name = event.target.value;
    })
  }, [cur, editGenerator]);

  const setMode = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    if (!cur) return;

    editGenerator(cur, (generator: GeneratorDefinition) => {
      generator.mode = event.target.value as GenerationType;
    })
  }, [cur, editGenerator]);

  const setPrompt = useCallback((event: React.ChangeEvent<HTMLInputElement>) => {
    if (!cur) return;

    editGenerator(cur, (generator: GeneratorDefinition) => {
      generator.prompt = event.target.value;
    })
  }, [cur, editGenerator]);

  if (!cur) {
    return null;
  }
  const { name, mode, prompt, uuid } = cur;

  return (
    <div style={{
      height: "100%",
      width: "100%",
      display: "flex",
      flexDirection: "column",
      alignItems: "center"
    }}>
      <TextField
        label="Name"
        value={name}
        onChange={setName}
        style={{
          marginTop: "8px",
          width: "100%"
        }}
      />
      <FormControl component="fieldset" style={{
        width: "100%",
        marginTop: "16px"
      }}>
        <FormLabel component="legend">Mode</FormLabel>
        <RadioGroup
          aria-label="mode"
          name="mode"
          value={mode}
          onChange={setMode}
          style={{
            flexDirection: "row"
          }}
        >
          <FormControlLabel value={GenerationType.CONTENT} control={<Radio />} label="Content" />
          <FormControlLabel value={GenerationType.STUBS} control={<Radio />} label="Stub" />
        </RadioGroup>
      </FormControl>
      <TextField
        label="Prompt"
        value={prompt}
        onChange={setPrompt}
        style={{
          marginTop: "16px",
          width: "100%",
        }}
        inputProps={{
          style: {
            height: "200px"
          }
        }}
        multiline
      />
      <Button
        variant="contained"
        size='small'
        onClick={() => {
          deleteGenerator(cur);
        }}
        style={{
          marginTop: "16px"
        }}
      >
        Delete Generator
      </Button>
    </div>
  )
}

function GeneratorList({ cur, setCur }: { cur: GeneratorDefinition | null, setCur: React.Dispatch<React.SetStateAction<GeneratorDefinition | null>> }) {
  const { generators, createGenerator } = useWriting();

  const contentGenerators = Object.keys(generators).filter((key) => {
    return generators[key].mode === GenerationType.CONTENT;
  })

  const stubGenerators = Object.keys(generators).filter((key) => {
    return generators[key].mode === GenerationType.STUBS;
  })

  console.log(contentGenerators, stubGenerators);

  return (
    <div style={{
      marginLeft: "-20px",
      marginRight: "5px",
      paddingTop: "10px",
      height: "100%",
      width: "150px",
      display: "flex",
      flexDirection: "column",
      alignItems: "left",
    }}>
      <div style={{
        width: "100%",
        height: "calc(100%)",
        display: "flex",
        justifyContent: "left",
        overflowY: "auto",
        marginTop: "-15px"
      }}>
        <List style={{ width: "100%" }}>
          <ListItemText style={{ marginLeft: "5px" }}>
            Content
          </ListItemText>
          {
            contentGenerators.map((key, index) => (
              <GeneratorLabel generatorKey={key} index={index} setCur={setCur} cur={cur} />
            ))
          }
          <Divider
            style={{
              marginTop: "10px",
              marginBottom: "10px"
            }}
          />
          <ListItemText style={{ marginLeft: "5px" }}>
            Stubs
          </ListItemText>
          {
            stubGenerators.map((key, index) => (
              <GeneratorLabel generatorKey={key} index={index} setCur={setCur} cur={cur} />
            ))
          }
        </List>
      </div>
      <Button
        variant="contained"
        size='small'
        style={{ width: "100%" }}
        onClick={() => {
          createGenerator();
        }}
      >
        New Generator
      </Button>
    </div>
  )
}

function GeneratorLabel(props: { generatorKey: string, index: number, setCur: React.Dispatch<React.SetStateAction<GeneratorDefinition | null>>, cur: GeneratorDefinition | null }): JSX.Element {
  const { generators } = useWriting();
  const generator = generators[props.generatorKey];
  console.log("Generator", generator, "with key", props.generatorKey);
  return (
    <ListItem dense key={props.index} button onClick={() => props.setCur(generator)} selected={props.cur?.uuid === generator?.uuid}>
      <ListItemText style={{
        // Dark colors to distinguish between stub and content generators, blue for content, green for stubs
        color: generator?.mode === GenerationType.CONTENT ? "#234677" : "#237746"
      }} primary={generator?.name} />
    </ListItem>
  );
}
