import React, { useState } from 'react';
import { Box, Typography } from '@mui/material';
import { AppBar, Toolbar } from '@mui/material';
import Modal from '@mui/material/Modal';
import { SettingsWindow } from './Settings';
import { Button } from '@mui/material';

export const MODAL_STYLE = {
  position: 'absolute' as 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: "400px",
  maxWidth: "calc(100vw - 100px)",
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  p: 4,
};
export function GPTMenuBar(props: { children: React.ReactNode; label: string, metaOpen: boolean; }) {
  const [settingsOpen, setSettingsOpen] = useState(false);
  return (
    <>
      <Modal open={settingsOpen} onClose={() => setSettingsOpen(false)}>
        <Box sx={MODAL_STYLE}>
          <SettingsWindow />
          <div style={{ height: "24px" }} />
          <Button
            style={{
              width: "calc(100% + 20px)",
              marginLeft: "-10px",
            }}
            variant="contained"
            onClick={() => setSettingsOpen(false)}
          >
            Close
          </Button>
        </Box>
      </Modal >

      <AppBar position="static" sx={{
        backgroundColor: '#191919',
        color: 'white',
        width: '100%',
        height: "48px",
        // Center content vertically
        display: "flex",
        justifyContent: "center",
        // alignItems: "center",
        // No drop shadow
        boxShadow: 'none',
      }}>
        <Toolbar>
          <Typography variant="h6" component="div" sx={{
            flexGrow: 1,
            marginRight: "12px",
            // paddingLeft: "36px",
            // Scale down if the window is too small
            fontSize: { xs: '1.1em', sm: '1.2em', md: '1.25em' },
          }}>
            {props.label}
          </Typography>
          {props.children}
          <Button variant="contained" onClick={() => setSettingsOpen(true)} sx={{
            marginRight: { xs: '2px', sm: '4px', md: '6px' },
          }}>
            Settings
          </Button>
        </Toolbar>
      </AppBar>
    </>
  );
}
