export enum Mode {
  Discussion = "Discussion",
  Assistant = "Assistant",
  Writer = "Writer",
}

function DuplicateTab() {
  // Choose a random quote
  const quotes = [
    "You cheated on me? When I specifically asked you not to?",
    "I'm not mad, I'm disappointed.",
    "Someday I'll fix the underlying problem. But alas, today is not that day."
  ]
  return (
    <div className="duplicate-tab">
      <h1>Looks like somebody opened another tab.</h1>
      <h2>{quotes[Math.floor(Math.random() * quotes.length)]}</h2>
      <p>This tab has been deactivated to ensure you don't lose any data.</p>
      <h3><a href=".">Refresh this page</a> if you want to use this tab (and send this message to the imposter!)</h3>
    </div>
  );
}

