function GlobalMenu(props: { children: JSX.Element, setMode: (mode: Mode) => void }) {
  // A small button always floats on the top-left, opening a drawer with some global options
  const [drawerOpen, setDrawerOpen] = useState(false);

  const handleDrawerOpen = () => setDrawerOpen(true);

  return (
    <>
      <Button style={{
        position: "absolute",
        top: "12px",
        left: "0px",
        padding: "0px",
        margin: "0px",
        zIndex: 1000,
        // White
        color: "white"
      }} onClick={handleDrawerOpen}>
        <MenuIcon />
      </Button>
      <Drawer
        anchor="left"
        open={drawerOpen}
        onClose={() => setDrawerOpen(false)}
      >
        <List>
          <ListItemButton onClick={() => {
            props.setMode(Mode.Discussion);
            setDrawerOpen(false);
          }}>
            <ListItemText primary="Discussion" />
          </ListItemButton>
          <ListItemButton onClick={() => {
            props.setMode(Mode.Writer);
            setDrawerOpen(false);
          }}>
            <ListItemText primary="Writer" />
          </ListItemButton>
        </List>
      </Drawer>
      {props.children}
    </>
  );
}

export default Assistant;