import React, { useEffect, createRef, useState, useMemo, useCallback } from 'react';
import { DialogNode, useConversation } from '../conversation/context';
import { Grid, List, ListItem, ListItemButton, ListItemText } from '@mui/material';
import ContentEditable from 'react-contenteditable';
import { useEditor, EditorContent } from '@tiptap/react'
import StarterKit from '@tiptap/starter-kit'
import { unified } from 'unified'
import remarkParse from 'remark-parse'
import remarkRehype from 'remark-rehype'
import rehypeSanitize from 'rehype-sanitize'
import rehypeStringify from 'rehype-stringify'
import CloseIcon from '@mui/icons-material/Close';
import './markdown.scss';

const MODELS = [
    "gpt-3.5-turbo",
    "gpt-4",
];

export function Message({ node, onChange, handleClick }: {
    node: DialogNode;
    onChange: (text: string) => void;
    handleClick: (node: DialogNode, recurse: boolean) => void;
}) {
    const [hovering, setHovering] = useState(false);
    const [modelPickerOpen, setModelPickerOpen] = useState(false);
    const { state, dispatch, actions } = useConversation();
    // console.log("Message node:", node);
    const editor = useEditor({
        extensions: [
            StarterKit,
        ],
        // content: node.text,
        content: node.texts[node.selectedText]?.text
    })


    useEffect(() => {
        if (!editor) {
            return;
        }

        // Reference: https://github.com/ueberdosis/tiptap/issues/2403#issuecomment-1017840603
        editor.off("update");
        editor.on("update", ({ editor: updatedEditor }) => onChange(updatedEditor.getHTML()));
    }, [editor, onChange]);

    useEffect(() => {
        // When the state changes, update the editor
        if (!editor) {
            return;
        }

        if (editor.getHTML() !== node.texts[node.selectedText]?.text) {
            // const raw = node.text;
            const raw = node.texts[node.selectedText]?.text;
            // Convert the text to HTML
            unified()
                .use(remarkParse)
                .use(remarkRehype)
                // .use(rehypeSanitize)
                .use(rehypeStringify)
                .process(raw)
                .then((file: any) => {
                    editor.commands.setContent(file.value);
                });
        }
    }, [editor, node.selectedText, node.texts]);

    const handleClickModelName = useCallback((e: any) => {
        e.stopPropagation();
        console.log("Clicked on model name");
        setModelPickerOpen(!modelPickerOpen);
    }, [modelPickerOpen]);

    return (
        <div>
            <div className="message-div" style={{
                backgroundColor: node.role === 'user' ? '#cacaca' : '#e0e0e0',
                marginBottom: node.role === 'user' ? '2px' : '8px',
                borderRadius: '8px',
                padding: '8px',
                width: '100%',
            }}>
                <Grid container spacing={1}>
                    <Grid
                        onMouseEnter={() => setHovering(true)}
                        onMouseLeave={() => setHovering(false)}
                        item
                        xs={12}
                        style={{
                            marginBottom: "4px"
                        }}
                    >
                        <div
                            style={{ display: 'flex', justifyContent: 'space-between' }}>
                            <span>
                                {/* <b>{node.role[0].toUpperCase() + node.role.slice(1)}</b> */}
                                <span>
                                    <span onClick={handleClickModelName}>
                                        {node.role === "user" && <b>User</b>}
                                        {node.role === "system" && <b>System</b>}
                                        {node.role === "assistant" && <b>Assistant ({node.selectedText?.toUpperCase()})</b>}
                                    </span>
                                    {
                                        modelPickerOpen && node.role === "assistant" && (
                                            <div
                                                style={{
                                                    position: 'absolute',
                                                    backgroundColor: 'white',
                                                    zIndex: 1,
                                                    border: '1px solid #606060',
                                                    borderRadius: '8px',
                                                    padding: '4px',
                                                    marginTop: '4px',
                                                }}
                                            >
                                                {MODELS.map((model) => (
                                                    <div
                                                        onClick={(e: any) => {
                                                            e.stopPropagation();
                                                            dispatch({
                                                                type: 'update-node',
                                                                uuid: node.uuid,
                                                                updateFn: (node) => {
                                                                    node.selectedText = model;
                                                                    return node;
                                                                }
                                                            });
                                                            setModelPickerOpen(false);
                                                        }}
                                                    >
                                                        {model}
                                                    </div>
                                                ))}
                                            </div>
                                        )
                                    }
                                </span>
                                {/* <div style={{ width: "5%" }} /> */}
                                {/* Delete button */}
                                {(hovering && node.role === "user") &&
                                    <>
                                        <span
                                            style={{ marginLeft: "10px", color: '#606060', justifyContent: 'flex-end' }}
                                            onClick={(e: any) => {
                                                e.stopPropagation();
                                                dispatch({
                                                    type: 'delete-node',
                                                    uuid: node.uuid,
                                                })
                                            }}
                                        >
                                            <i>Delete</i>
                                        </span>
                                        <span
                                            style={{ marginLeft: "10px", color: '#606060', justifyContent: 'flex-end' }}
                                            onClick={(e: any) => {
                                                e.stopPropagation();
                                                actions.forkNode(node.uuid);
                                            }}
                                        >
                                            <i>Fork</i>
                                        </span>
                                        <span
                                            style={{ marginLeft: "10px", color: '#606060', justifyContent: 'flex-end' }}
                                            onClick={(e: any) => {
                                                e.stopPropagation();
                                                actions.rerunNode(node.uuid);
                                            }}
                                        >
                                            <i>Run</i>
                                        </span>
                                    </>
                                }
                                {(hovering && node.role === "assistant") &&
                                    <>
                                        <span
                                            style={{ marginLeft: "10px", color: '#606060', justifyContent: 'flex-end' }}
                                            onClick={(e: any) => {
                                                e.stopPropagation();
                                                actions.rerunNode(node.uuid);
                                            }}
                                        >
                                            <i>Run</i>
                                        </span>
                                    </>
                                }
                            </span>
                            {/* Spacer */}
                            <div style={{ width: '10%' }} />
                            <span style={{ color: '#606060', justifyContent: 'flex-end' }}>
                                <i>{node.summary}</i>
                            </span>
                        </div>
                    </Grid>
                    <Grid item xs={12} style={{
                        marginBottom: "-12px",
                    }}>
                        {node.role as unknown === "system" && (
                            // Simple text input, no markdown
                            // <div style={{ marginBottom: "12px", whiteSpace: 'pre-wrap', width: "100%", height: "100%" }}>
                            //     {node.text}
                            // </div>
                            <ContentEditable
                                // html={node.text}
                                html={node.texts[node.selectedText].text}
                                disabled={false}
                                onChange={(e: any) => onChange(e.target.value)}
                                style={{
                                    // Don't outline when focused
                                    outline: "none",
                                    marginBottom: "16px", whiteSpace: 'pre-wrap', width: "100%", height: "100%"
                                }}
                            />
                        )}
                        {node.role as unknown !== "system" && (
                            <EditorContent editor={editor} />
                        )}
                    </Grid>
                </Grid >
            </div>
            {
                node.uuid === state.currentNode && node.children.length > 0 && (
                    <List style={{ margin: "0px", padding: "0px" }}>
                        {node.role !== "user" && node.children.map((childUUID: string) => {
                            const child = state.nodes[childUUID];
                            return (
                                <MessageResponseItem
                                    key={childUUID}
                                    node={child}
                                    handleClick={handleClick}
                                />
                            )
                        })}
                    </List>
                )
            }
        </div >
    );
}

const MessageResponseItem = ({ node, handleClick }: {
    node: DialogNode;
    handleClick: (node: DialogNode, recurse: boolean) => void;
}) => {
    const { state, dispatch } = useConversation();

    // const primary = node.summary ? node.summary : node.text.slice(0, 100) + "...";
    const primary = node.summary ? node.summary : node.texts[node.selectedText].text.slice(0, 100) + "...";
    let secondary;
    if (node?.children[0] && state.nodes[node.children[0]].summary) {
        secondary = state.nodes[node.children[0]].summary;
    } else if (node?.children[0]) {
        // secondary = state.nodes[node.children[0]].text.slice(0, 100) + "...";
        secondary = state.nodes[node.children[0]].texts[node.selectedText].text.slice(0, 100) + "...";
    } else {
        secondary = "";
    }
    return (
        <ListItemButton
            style={{ marginLeft: "auto", marginRight: "auto", width: "calc(100% - 8px)", borderRadius: "4px" }}
            key={node.uuid}
            onClick={(e: any) => {
                e.stopPropagation();
                handleClick(node, e.shiftKey);
            }}
        >
            <ListItemText
                style={{ padding: "0px", margin: "0px" }}
                primary={primary}
                secondary={secondary}
            />
        </ListItemButton>
    );
}