import React, { useState, useCallback, useMemo, useEffect, useContext } from 'react';
import { parse, stringify } from 'yaml';
import { Message, iterableStreamGPT4Message } from '../openai_api/gpt4';
import {
    ASSISTANT_SYSTEM_MESSAGE,
    SUMMARIZER_SYSTEM_MESSAGE,
    CONVERSATION_NAME_SYSTEM_MESSAGE,
    CONVERSATION_SUMMARY_SYSTEM_MESSAGE,
    JUDGE_SYSTEM_MESSAGE
} from '../prompts';
import { Settings, SettingsContext, useSettings } from '../Settings';
import { produce } from 'immer';
// import { reconcileState, loadRoots, loadNodes } from './dexie_db';
import { reconcileState, loadRoots, loadNodes } from './db';

export interface ConversationState {
    loaded: boolean;
    nodes: { [key: string]: DialogNode };
    roots: { [key: string]: DialogRoot };
    currentNode: string;
    currentRoot: string;
    trash: string[];
}

export interface DialogRoot {
    uuid: string;
    name: string;
    summary: string;
    systemMessage: string;
    currentNode: string;
}

type USER = "user" | "assistant" | "system";

export interface DialogNode {
    uuid: string;
    root: string;
    parent: string;
    role: USER;
    // text: string;
    texts: { [key: string]: DialogResponse };
    selectedText: string;
    summary: string;
    children: string[];
    evaluation: "correct" | "incorrect" | null;
}

export interface DialogResponse {
    model: string | null;
    text: string;
}

// Define some simple tree operations
export const getRoot = (node: DialogNode, nodes: { [key: string]: DialogNode }): DialogNode => {
    if (node.parent === null) {
        return node;
    }
    return getRoot(nodes[node.parent], nodes);
}

export const getAncestry = (node: DialogNode, nodes: { [key: string]: DialogNode }): DialogNode[] => {
    console.log("Getting ancestry of node:", node);
    if (!node) {
        console.error("Warning: getAncestry called with null node");
        return [];
    }
    // Get the list of nodes from the root node to this node, in order
    const ancestry = [node];
    while (ancestry[0].parent !== "") {
        ancestry.unshift(nodes[ancestry[0].parent]);
    }
    return ancestry;
}

export const getChildren = (node: DialogNode, nodes: { [key: string]: DialogNode }): DialogNode[] => {
    return node.children.map(uuid => nodes[uuid]);
}

export const getAllChildren = (node: DialogNode, nodes: { [key: string]: DialogNode }): DialogNode[] => {
    const children = getChildren(node, nodes);
    return children.concat(children.flatMap(child => getAllChildren(child, nodes)));
}

type ConversationAction =
    // CRUD for roots
    | { type: "create-root", uuid: string }
    | { type: "update-root", uuid: string, updateFn: (root: DialogRoot) => DialogRoot }
    | { type: "delete-root", uuid: string }

    // CRUD for nodes
    | { type: "create-node", uuid: string, parent: string, role: USER, model: string, response: string }
    | { type: "update-node", uuid: string, updateFn: (node: DialogNode) => DialogNode }
    | { type: "delete-node", uuid: string }
    | { type: "fork-node", uuid: string, model: string, run: true, dispatch: React.Dispatch<ConversationAction> }

    // Navigation
    | { type: "set-current-root", uuid: string }
    | { type: "set-current-node", uuid: string, recurse: boolean }

    // AI response
    | { type: "respond-to-user", uuid: string, model: string, dispatch: React.Dispatch<ConversationAction>, summarizeUser: boolean }
    | { type: "summarize-node", uuid: string, model: string, dispatch: React.Dispatch<ConversationAction> }
    | { type: "evaluate-node", uuid: string, model: string, dispatch: React.Dispatch<ConversationAction> }
    // Other
    // Note: save-state is done automatically when we update the state
    | { type: "load-state", roots: DialogRoot[], nodes: DialogNode[], currentRoot: string, currentNode: string };

const initialState: ConversationState = {
    loaded: false,
    nodes: {},
    roots: {},
    currentNode: "",
    currentRoot: "",
    trash: []
};

const ConversationContext = React.createContext<{
    state: ConversationState;
    dispatch: React.Dispatch<ConversationAction>;
    actions: {
        userResponse: (nodeUUID: string, response: string, role: USER) => void;
        forkNode: (nodeUUID: string) => void;
        rerunNode: (nodeUUID: string) => void;
    }
}>({
    state: initialState,
    dispatch: () => { },
    actions: {
        userResponse: () => { },
        forkNode: () => { },
        rerunNode: () => { }
    }
});

const processGPT4Message = async (
    messages: Message[],
    model: string,
    updateFn: (response: string, dispatch: React.Dispatch<ConversationAction>) => void,
    dispatch: React.Dispatch<ConversationAction>
) => {
    for await (const resp of iterableStreamGPT4Message(messages, model)) {
        updateFn(resp.whole, dispatch);
    }
};

const evaluateNode = async (
    role: USER,
    text: string,
    context: string,
    model: string,
): Promise<"correct" | "incorrect"> => {
    return "correct";
}

const conversationReducer = produce((draft: ConversationState, action: ConversationAction) => {
    switch (action.type) {
        case "create-root":
            // Create a new root
            const newRoot: DialogRoot = {
                uuid: action.uuid,
                name: "Dialog Root",
                summary: "Dialog tree summary",
                systemMessage: ASSISTANT_SYSTEM_MESSAGE,
                currentNode: action.uuid + "-root",
            };

            // Add it to the tree
            draft.roots[newRoot.uuid] = newRoot;

            // Create a new node and add it to the tree
            const newRootNode: DialogNode = {
                uuid: action.uuid + "-root",
                root: newRoot.uuid,
                parent: "",
                role: "assistant",
                // text: "This should not be visible.",
                texts: {
                    system: {
                        text: "This should not be visible.",
                        model: null
                    }
                },
                selectedText: "system",
                // text: "Hello! I'm your assistant. How can I help you?",
                summary: "",
                children: [],
                evaluation: null
            };
            draft.nodes[newRootNode.uuid] = newRootNode;

            // Set the current root and node
            draft.currentRoot = newRoot.uuid;
            draft.currentNode = newRootNode.uuid;

            break;

        case "update-root":
            draft.roots[action.uuid] = action.updateFn(draft.roots[action.uuid]);
            break;

        case "delete-root":
            // If the current root was deleted, reset it
            if (draft.currentRoot === action.uuid) {
                draft.currentRoot = "";
            }

            // If the current node was deleted, reset it
            if (draft.nodes[draft.currentNode]?.root === action.uuid) {
                draft.currentNode = "";
            }

            // Delete all nodes with the root as its root
            for (const node of Object.values(draft.nodes)) {
                if (node.root === action.uuid) {
                    delete draft.nodes[node.uuid];
                    draft.trash.push(node.uuid);
                }
            }

            // Delete the root
            delete draft.roots[action.uuid];
            draft.trash.push(action.uuid);
            break;

        case "create-node":
            // Create a new node
            const newNode: DialogNode = {
                uuid: action.uuid,
                root: draft.nodes[action.parent].root,
                parent: action.parent,
                role: action.role,
                texts: {
                    [action.model]: {
                        text: action.response,
                        model: null
                    }
                },
                selectedText: action.model,
                summary: "",
                children: [],
                evaluation: null
            };

            // Add it to the tree
            draft.nodes[newNode.uuid] = newNode;

            // Add it to the parent's children
            draft.nodes[action.parent].children.push(newNode.uuid);

            // Auto select the new node
            draft.currentNode = newNode.uuid;
            break;

        case "update-node":
            const updated = action.updateFn(draft.nodes[action.uuid]);
            draft.nodes[action.uuid] = updated;
            break;

        case "delete-node":
            const parent = draft.nodes[action.uuid].parent;
            const root = draft.nodes[action.uuid].root;

            let resetCurrentNode = false;
            // Delete all children of the node
            for (const node of getAllChildren(draft.nodes[action.uuid], draft.nodes)) {
                if (node.uuid === draft.currentNode) {
                    resetCurrentNode = true;
                }
                delete draft.nodes[node.uuid];
                draft.trash.push(node.uuid);
            }

            // Delete the node
            if (draft.currentNode === action.uuid) {
                resetCurrentNode = true;
            }
            delete draft.nodes[action.uuid];
            draft.trash.push(action.uuid);

            // Remove the node from its parent's children array
            if (parent) {
                draft.nodes[parent].children = draft.nodes[parent].children.filter(child => child !== action.uuid);
            } else {
                throw new Error("Cannot delete root node");
            }

            if (resetCurrentNode) {
                // Set the current node to the parent of the deleted node
                if (parent) {
                    draft.currentNode = parent;
                } else {
                    draft.currentNode = root;
                }
            }

            break;

        case "set-current-root":
            console.log("Selected root:", action.uuid)
            // Set the current root to the given root
            draft.currentRoot = action.uuid;
            draft.currentNode = action.uuid;

            // // Set the current node to the root node
            // const rootNode = Object.values(draft.nodes).find(node => node.root === action.uuid && !node.parent);
            // if (rootNode) {
            //     draft.currentNode = rootNode.uuid;
            // } else {
            //     console.warn("No root node found for root:", action.uuid);
            // }

            // Set the current node to the root's current node
            draft.currentNode = draft.roots[action.uuid].currentNode;
            break;

        case "set-current-node":
            console.log("Selected node:", action.uuid)

            // If the selected node isn't a valid node, complain in the console and return
            if (!draft.nodes[action.uuid]) {
                throw new Error("Node does not exist: " + action.uuid);
            }

            // If the selected node is a user node, select its assistant child
            if (draft.nodes[action.uuid].role === "user") {
                const assistantChild = draft.nodes[action.uuid].children.find(child => draft.nodes[child].role === "assistant");
                if (assistantChild) {
                    draft.currentNode = assistantChild;
                } else {
                    // Message has no response yet (this is ok!)
                    draft.currentNode = action.uuid;
                }
            } else {
                draft.currentNode = action.uuid;
            }

            // Continue down the tree until we reach a leaf or a node with more than one child
            if (action.recurse) {
                while (draft.nodes[draft.currentNode].children.length === 1) {
                    draft.currentNode = draft.nodes[draft.currentNode].children[0];
                }
            }

            // Set the current root's current node to this node
            draft.roots[draft.nodes[action.uuid].root].currentNode = action.uuid;
            break;

        case "respond-to-user":
            // Get the node
            const node_uuid = action.uuid;
            const parent_uuid = draft.nodes[node_uuid].parent;

            // Get message chain from node to root
            const pathToRoot = getAncestry(draft.nodes[action.uuid], draft.nodes);

            // Construct the messages
            const messages: Message[] = [{
                role: "system",
                content: draft.roots[draft.nodes[node_uuid].root].systemMessage
            }];

            for (let i = 1; i < pathToRoot.length; i++) {
                const node = pathToRoot[i];
                if (!node.texts[action.model]) {
                    // Create a new text with this model
                    node.texts[action.model] = {
                        text: "",
                        model: action.model
                    };
                }
                messages.push({
                    role: node.role,
                    content: node.texts[node.selectedText].text,
                });
            }

            // Respond to the user
            if (parent_uuid && action.summarizeUser) {
                // action.summarizeNode(draft.nodes[node.parent]);
                action.dispatch({
                    type: "summarize-node",
                    uuid: parent_uuid,
                    model: action.model,
                    dispatch: action.dispatch
                });
            }

            const fn = async () => {
                await processGPT4Message(messages, action.model, (responseText, dispatch) => {
                    dispatch({
                        type: "update-node",
                        uuid: node_uuid,
                        updateFn: (node: DialogNode) => {
                            if (!node) {
                                return node;
                            }
                            // node.text = responseText;
                            node.texts[action.model] = {
                                text: responseText,
                                model: action.model
                            };
                            return node;
                        }
                    });
                }, action.dispatch);

                // Summarize the nodes
                action.dispatch({
                    type: "summarize-node",
                    uuid: node_uuid,
                    model: action.model,
                    dispatch: action.dispatch
                });
            };

            fn();
            break;

        case "summarize-node":
            // Get the chat history up to this node
            // const ancestry = getAncestry(node, state.nodes);
            if (!draft.nodes[action.uuid]) {
                // Node doesn't exist - this is ok, it just means we've deleted the node
                return;
            }

            // Get role, text (current node), and context (all previous summaries)
            const ancestry = getAncestry(draft.nodes[action.uuid], draft.nodes);
            const cur = ancestry[ancestry.length - 1];
            const role = cur.role;
            const text = cur.texts[cur.selectedText].text;
            const context = ancestry.slice(0, ancestry.length - 1).map(node => node.summary).join("\n");

            // Call with GPT-4
            const summarizeMessages: Message[] = getMessagesFromNode(role, context, text);

            const summarizeFn = async () => {
                await processGPT4Message(summarizeMessages, action.model, (responseText, dispatch) => {
                    dispatch({
                        type: "update-node",
                        uuid: action.uuid,
                        updateFn: (node: DialogNode) => {
                            if (!node) {
                                return node;
                            }
                            node.summary = responseText;
                            return node;
                        }
                    });
                }, action.dispatch);
            };
            summarizeFn();

            // if (role === "assistant") {
            //     const judgement: Message[] = judgeNode(role, context, text);
            //     const judgeFn = async () => {
            //         await processGPT4Message(judgement, "gpt-3.5-turbo", (responseText, dispatch) => {
            //             console.log("Judgement:", responseText);
            //             dispatch({
            //                 type: "update-node",
            //                 uuid: action.uuid,
            //                 updateFn: (node: DialogNode) => {
            //                     if (!node) {
            //                         return node;
            //                     }
            //                     node.evaluation = responseText as "correct" | "incorrect";
            //                     return node;
            //                 }
            //             });
            //         }, action.dispatch);
            //     }
            //     judgeFn();
            // }

            break;

        case "evaluate-node":
            // Get role, text (current node), and context (all previous summaries)
            const evalAncestry = getAncestry(draft.nodes[action.uuid], draft.nodes);
            // const evalRole = evalAncestry[evalAncestry.length - 1].role;
            // const evalText = evalAncestry[evalAncestry.length - 1].text;
            const evalNode = evalAncestry[evalAncestry.length - 1];
            const evalRole = evalNode.role;
            const evalText = evalNode.texts[evalNode.selectedText].text;
            const evalContext = evalAncestry.slice(0, evalAncestry.length - 1).map(node => node.summary).join("\n");


            const evalFn = async () => {
                // Get the evaluation
                const evaluation = await evaluateNode(evalRole, evalText, evalContext, action.model);

                // Update the node
                action.dispatch({
                    type: "update-node",
                    uuid: action.uuid,
                    updateFn: (node: DialogNode) => {
                        if (!node) {
                            return node;
                        }
                        node.evaluation = evaluation
                        return node;
                    }
                });
            };
            evalFn();
            break;

        case "load-state":
            // Load the roots and nodes
            for (const root of action.roots) {
                draft.roots[root.uuid] = root;
            }
            for (const node of action.nodes) {
                draft.nodes[node.uuid] = node;
            }

            // Set the current root and node
            draft.currentRoot = action.currentRoot;
            draft.currentNode = action.currentNode;

            // Set the loaded flag
            draft.loaded = true;

            break;

        case "fork-node":
            // Get the node
            const node = draft.nodes[action.uuid];
            const uuid = crypto.randomUUID();

            action.dispatch({
                type: "create-node",
                uuid: uuid,
                parent: node.uuid,
                role: "assistant",
                response: "",
                model: action.model
            });
            break;

        default:
            // Trick to ensure we've handled all actions
            // @ts-expect-error
            throw new Error(`Unknown action type: ${action.type}`);
    }
});

export const loadState = async (dispatch: React.Dispatch<ConversationAction>) => {
    // Load all roots and nodes
    const roots = await loadRoots();
    const nodes = await loadNodes();

    // Load the current root and node from local storage
    const currentRoot = localStorage.getItem('currentRoot');
    const currentNode = localStorage.getItem('currentNode');

    dispatch({
        type: "load-state",
        roots: roots,
        nodes: nodes,
        currentRoot: currentRoot ? currentRoot : "",
        currentNode: currentNode ? currentNode : "",
    });
}

const ConversationProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
    const [state, dispatch] = React.useReducer(conversationReducer, initialState);
    const settings = useSettings();


    // Load the database
    useEffect(() => {
        loadState(dispatch);
    }, []);

    // Not very efficient, but it works for now
    useEffect(() => {
        reconcileState(state);
    }, [state]);

    // Update local storage when the current root or node changes
    useEffect(() => {
        state.currentRoot && localStorage.setItem('currentRoot', state.currentRoot);
        state.currentNode && localStorage.setItem('currentNode', state.currentNode);
    }, [state.currentRoot, state.currentNode]);

    const respond = useCallback((uuid: string, role: USER, model: string = settings.model) => {
        // Move to the new node
        dispatch({
            type: "set-current-node",
            uuid: uuid,
            recurse: false
        });

        if (role === "assistant") {
            // Re-generate this response
            dispatch({
                type: "respond-to-user",
                uuid: uuid,
                model: model,
                dispatch: dispatch,
                summarizeUser: false
            })
        }

        if (role === "user") {
            // Create a node to hold the response
            const responseUUID = crypto.randomUUID();
            dispatch({
                type: "create-node",
                uuid: responseUUID,
                parent: uuid,
                role: "assistant",
                response: "",
                model: model
            });

            // Move to the new node
            dispatch({
                type: "set-current-node",
                uuid: responseUUID,
                recurse: false
            });

            dispatch({
                type: "respond-to-user",
                uuid: responseUUID,
                model: settings.model,
                dispatch: dispatch,
                summarizeUser: true
            })
        }
    }, [settings.model]);

    const userResponse = useCallback(async (parentUUID: string, response: string, role: USER, allowResponse: boolean = true) => {
        // Create the new node
        const uuid = crypto.randomUUID();
        dispatch({
            type: "create-node",
            uuid: uuid,
            parent: parentUUID,
            role: role,
            response: response,
            model: settings.model
        });

        // Respond
        if (allowResponse) {
            respond(uuid, role);
        }
    }, [respond, settings.model]);

    const forkNode = useCallback((uuid: string) => {
        // Fork the node and run it immediately
        const node = state.nodes[uuid];

        if (node === null) {
            throw new Error(`Node with UUID ${uuid} not found`);
        }

        // User response mimicked
        // userResponse(node.parent, node.text, node.role, false);
        userResponse(node.parent, node.texts[node.selectedText].text, node.role, false);
    }, [userResponse, state.nodes]);

    const rerunNode = useCallback((uuid: string) => {
        // Re-run the selected node
        const node = state.nodes[uuid];

        if (node === null) {
            throw new Error(`Node with UUID ${uuid} not found`);
        }

        respond(uuid, node.role, node.selectedText);
    }, [respond, state.nodes]);

    return (
        <ConversationContext.Provider value={{
            state: state,
            dispatch: dispatch,
            actions: {
                userResponse, forkNode, rerunNode
            }
        }}>
            {children}
        </ConversationContext.Provider>
    );
};

export const useConversation = () => {
    const context = useContext(ConversationContext);
    if (context === undefined) {
        throw new Error('useConversation must be used within a ConversationProvider');
    }
    return context;
}

function getMessagesFromNode(role: string, context: string, text: string): Message[] {
    return [
        {
            role: "system",
            content: SUMMARIZER_SYSTEM_MESSAGE,
        },
        {
            role: "user",
            content: [
                "This is a message from the " + role + ".",
                "",
                "*** CONTEXT [PREVIOUS SUMMARIES] ***",
                context,
                "*** END CONTEXT ***",
                "",
                "*** MESSAGE ***",
                text,
                "*** END MESSAGE ***",
                "",
                "Please rewrite this message in 10 words or less, in the same person of speech as the original message.",
            ].join("\n")
        }
    ];
}

function judgeNode(role: string, context: string, text: string): Message[] {
    return [
        {
            role: "system",
            content: JUDGE_SYSTEM_MESSAGE,
        },
        {
            role: "user",
            content: [
                "This is a message from the " + role + ".",
                "",
                "*** CONTEXT [PREVIOUS SUMMARIES] ***",
                context,
                "*** END CONTEXT ***",
                "",
                "*** MESSAGE ***",
                text,
                "*** END MESSAGE ***",
                "",
                "Please judge the message on the appropriate characteristics as described previously.",
            ].join("\n")
        }
    ];
}

export default ConversationProvider;
