import React, { useCallback, useMemo } from 'react';
import { useConversation, getChildren, getAncestry, DialogNode } from '../conversation/context';
import { List, ListItem, ListItemText } from '@mui/material';
import { AutoScrollChatWindow } from './AutoScrollChatWindow';
import { ChatInput } from "./ChatInput";
import { Message } from "./Message";

export const ChatWindow = () => {
    const { state, dispatch } = useConversation();

    const handleClick = useCallback((node: DialogNode, recurse: boolean) => {
        console.log("Clicked on:", node);
        if (node.role === 'user' && node.children.length === 1) {
            const assistantNodeUUID = node.children.find((child) => state.nodes[child].role === 'assistant');
            if (assistantNodeUUID) {
                console.log("Found assistant node");
                // setCurrentNode(assistantNode);
                dispatch({
                    type: 'set-current-node',
                    uuid: assistantNodeUUID,
                    recurse: recurse
                })
                return;
            }
            console.log("Didn't find assistant node");
        }

        console.log("Setting current node");
        // setCurrentNode(node);
        dispatch({
            type: 'set-current-node',
            uuid: node.uuid,
            recurse: recurse
        })
    }, [state.nodes, dispatch]);

    const path = useMemo(() => {
        // if (!state || !state.currentNode || !state.nodes[state.currentNode]) {
        //     return [];
        // }
        console.log(state);
        if (!state) {
            console.log("No state");
            return [];
        }

        if (!state.currentNode) {
            console.log("No current node");
            return [];
        }

        if (!state.nodes[state.currentNode]) {
            console.log("No current node in nodes. Looking for node '" + state.currentNode + "'");
            return [];
        }

        return getAncestry(
            state.nodes[state.currentNode],
            state.nodes,
        )
    }, [state]);

    // console.log("Path:", path);

    const root = useMemo(() => {
        return state.roots[state.nodes[state.currentNode]?.root];
    }, [state]);

    return (
        <div style={{
            height: "100%",
            width: "100%",
            maxWidth: "1400px",
            marginLeft: "auto",
            marginRight: "auto",
        }}>
            <div style={{ paddingLeft: "2px", paddingRight: "2px" }}>
                <AutoScrollChatWindow height="calc(100vh - 220px)" width="100%">
                    {/* <div style={{
                        width: "100%",
                        // Center text
                        display: "flex",
                        justifyContent: "center",
                        alignItems: "center",
                        textAlign: "center",
                        // Column
                        flexDirection: "column",
                        padding: "0px",
                        margin: "0px",
                        paddingTop: "12px"
                    }}>
                        <span style={{ fontSize: "24px", fontWeight: "bold", marginBottom: "4px" }}>
                            {root?.name}
                        </span>
                        {root?.summary}
                    </div> */}
                    <div style={{ height: '16px' }} />
                    {path.map((node, index) => (
                        <div
                            key={node.uuid}
                            onClick={(e) => {
                                e.stopPropagation();
                                handleClick(node, false);
                            }}
                            style={{
                                width: "calc(100% - 28px)", // 8px is the margin
                                margin: "6px",
                                cursor: 'pointer',
                            }}
                        >
                            {index === 0 && (
                                <Message
                                    node={{
                                        ...node,
                                        role: "system" as any,
                                        // text: root.systemMessage
                                        texts: {
                                            "system": {
                                                text: root.systemMessage,
                                                model: "system"
                                            }
                                        }
                                    }}
                                    onChange={(text) => {
                                        // Set the root system message
                                        dispatch({
                                            type: "update-root",
                                            uuid: root.uuid,
                                            updateFn: (root) => {
                                                root.systemMessage = text;
                                                return root;
                                            }
                                        })
                                    }}
                                    handleClick={handleClick}
                                />
                            )}
                            {index > 0 && (
                                <Message
                                    node={node}
                                    onChange={(text) => {
                                        // Set the node text
                                        dispatch({
                                            type: "update-node",
                                            uuid: node.uuid,
                                            updateFn: (node) => {
                                                // node.text = text;
                                                node.texts[node.selectedText].text = text;
                                                return node;
                                            }
                                        })
                                    }}
                                    handleClick={handleClick}
                                />
                            )}
                        </div>
                    ))}
                    {!state.currentNode && (
                        <div style={{ padding: '8px', borderRadius: '10px', backgroundColor: '#e0e0e0', marginBottom: '8px' }}>
                            <h1>
                                Please start or select a conversation!
                            </h1>
                        </div>
                    )}
                </AutoScrollChatWindow>
            </div >
            <div className="chat-input" style={{ height: '252px', padding: "4px" }}>
                <ChatInput />
            </div>
        </div >
    );
};
